.intro{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0px;
    padding-top: 100px;
    width: 100%;
    height: 100vh;
    position: relative;
}

.particles canvas{
    z-index: -2;
    position: absolute;
}

.intro img{
    height: 250px;
    border-radius: 50%;
    z-index: 1;
    /* margin-top: 60px;  */
}

.intro h1{
    text-align: center;
    width: 70%;
    font-size: max(4vw, 20px);
    font-weight: 600;
    z-index: 1;
}

.intro h1 span{
    background: linear-gradient(264deg, #4040bf -5.09%, #ff0000 106.28%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    z-index: 1;
}

.intro p{
    font-size: max(1.3vw, 12px);
    z-index: 1;
}

.action{
    display: flex;
    align-items: center;
    font-size: max(2vw, 15px);
    font-weight: 500px;
    margin-bottom: 50px;
    gap: 25px;
    z-index: 1;
}

.anchorLink{
    text-decoration: none;
    color: white;
}

.connect{
    padding: 1vw 2vw;
    border-radius: 50px;
    background: linear-gradient(264deg, #4040bf -5.09%, #ff0000 106.28%);
    cursor: pointer;
}

.resume{
    padding: 1vw 2vw;
    border-radius: 50px;
    border: 2px solid #FFF;
    cursor: pointer;
}

.connect:hover{
    background:none;
    border-radius: 50px;
    border: 2px solid #FFF;
}

.resume:hover{
    border: none;
    border-radius: 50px;
    background: linear-gradient(264deg, #e07109 -5.09%, #690650e2 106.28%);
}


@media(max-width: 768px){
    .connect{
        padding: 20px 20px;
    }
    .resume{
        padding: 20px 20px;
    }
}